/* eslint-disable*/
/* eslint-disable no-restricted-imports */
import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { Tooltip } from "@material-ui/core";
import * as Yup from "yup";
import {
  PagingState,
  IntegratedPaging,
  FilteringState,
  IntegratedFiltering,
  RowDetailState,
  SortingState,
  IntegratedSorting,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  Table,
  Toolbar,
  TableFilterRow,
  TableHeaderRow,
  TableRowDetail,
  PagingPanel,
  TableColumnVisibility,
  ColumnChooser,
} from "@devexpress/dx-react-grid-bootstrap4";
import {
  Button,
  Modal,
  Form,
} from "react-bootstrap";
import "@devexpress/dx-react-grid-bootstrap4/dist/dx-react-grid-bootstrap4.css";
import config from "../../../../config/config";
import { useFormik } from "formik";


const TableComponent = ({ ...restProps }) => (
  <Table.Table
    {...restProps}
    className="table table-hover table-head-custom table-vertical-center overflow-hidden"
  />
);

const filterRowMessages = {
  filterPlaceholder: "Filtra...",
};

export const TabellaDipendenti = (props) => {
  const { user } = useSelector((state) => state.auth);

  const [showDelete, setShowDelete] = useState(false);
  const handleCloseDelete = () => setShowDelete(false);
  const handleShowDelete = () => setShowDelete(true);

  const [showEdit, setShowEdit] = useState(false);
  const handleCloseEdit = () => setShowEdit(false);
  const handleShowEdit = () => setShowEdit(true);

  const RowDetail = ({ row }) => {
    const [province, setProvince] = useState([]);
    const [country, setCountry] = useState([]);
    const [regione, setRegione] = useState([]);
    const [city, setCity] = useState([]);

    //STATO PER GESTIRE LA SELEZIONE DI NAZIONE, REGIONE, CITT
    const [selectedCountry, setSelectedCountry] = useState(-1);
    const [selectedRegion, setSelectedRegion] = useState(-1);
    const [selectedProvince, setSelectedProvince] = useState(-1);
    const [selectedCity, setSelectedCity] = useState(-1);

    //STATO PER GESTIRE I MESSAGGI DI ERRORE
    const [message] = useState(false);
    const [emailMessage, setEmailMessage] = useState(false);
    const [emailAziendaleMessage, setEmailAziendaleMessage] = useState(false);
    const [phoneMessage, setPhoneMessage] = useState(false);

    //VALIDAZIONE FORM

    const getInputClassesForHandle = (fieldname) => {
      if (formik.touched[fieldname] && formik.errors[fieldname]) {
        return "is-invalid";
      }

      if (formik.touched[fieldname] && !formik.errors[fieldname]) {
        return "is-valid";
      }
      return "";
    };

    const validationSchema = Yup.object({
      nome: Yup.string()
        .required("Inserisci un nome valido")
        .matches(/^[A-Za-z]+$/, "Il nome non pu contenere numeri o simboli"),
      cognome: Yup.string()
        .required("Inserisci un cognome valido")
        .matches(
          /^[A-Za-z]+$/,
          "Il cognome non pu contenere numeri o simboli"
        ),
      email: Yup.string()
        .email("Inserire una mail valida")
        .required("Inserire una mail valida"),
      emailAziendale: Yup.string()
        .email("Inserire una mail valida")
        .notRequired("Inserire una mail valida"),
      phone: Yup.string()
        .matches(/^\d+$/, "Il numero di telefono non pu contenere lettere")
        .required("Inserire un numero di telefono valido"),
      zip: Yup.string()
        .matches(/^\d+$/, "Il CAP non pu contenere lettere")
        .required("Inserire un CAP valido"),
      vatNumber: Yup.string()
        .matches(/^\d+$/, "La partita IVA non pu contenere lettere")
        .min(11, "La partita IVA deve essere composta da 11 numeri")
        .max(11, "La partita IVA deve essere composta da 11 numeri")
        .notRequired(),
      iban: Yup.string()
        .min(27, "L'iban deve essere lungo almeno 27 caratteri")
        .max(32, "L'iban pu avere massimo 32 caratteri")
        .notRequired(),
    });

    //STATO PER GESTIRE IL CARICAMENTO DEI FILE
    const [file, setFile] = useState();

    //STATO PER GESTIRE IL FORM
    var customValues;
    if (props.customfield !== []) {
      var newValues = {};
      props.customfield.map((e) => {
        var k = e.slug;
        var result = row.customfield[e.slug];
        newValues[k] = result;
      });
      customValues = newValues;
    } else {
      customValues = {};
    }

    const [editForm, setEditForm] = useState({
      idDipendente: row.idUser,
      nome: row.nome,
      cognome: row.cognome,
      birthday: row.birth !== undefined ? row.birth : "1970-01-01",
      password: "",
      phone: row.phone !== undefined ? row.phone : "",
      email: row.email !== undefined ? row.email : "",
      emailAziendale:
        row.emailAziendale !== undefined ? row.emailAziendale : "",
      country: selectedCountry,
      regione: selectedRegion,
      city: selectedCity,
      zip: row.zip,
      address: row.address,
      vatNumber: row.vatNumber,
      iban: row.iban,
      idRole: row.idRole,
      idDepartment: row.idDepartment,
    });

    const initialValues = { ...editForm, ...customValues };

    const GetCountry = async () => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
      };

      var rawData = await fetch(
        config.apiUrl + "calendar/GetCountry.php",
        requestOptions
      );
      var data = await rawData.json();
      setCountry(data);
    };

    const GetProvince = async () => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({
          idRegione: selectedRegion,
        }),
      };

      var rawData = await fetch(
        config.apiUrl + "calendar/GetProvince.php",
        requestOptions
      );
      var data = await rawData.json();
      setProvince(data);
    };

    const GetCity = async () => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({
          idProvincia: selectedProvince,
        }),
      };
      var rawData = await fetch(
        config.apiUrl + "calendar/GetCity.php",
        requestOptions
      );
      var data = await rawData.json();
      setCity(data);
    };

    const GetRegione = async () => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({}),
      };
      var rawData = await fetch(
        config.apiUrl + "calendar/GetRegione.php",
        requestOptions
      );
      var data = await rawData.json();
      setRegione(data);
    };

    const DeleteUser = () => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({
          idUtente: user.id,
          idOrganization: user.id_organization,
          idDipendente: row.idUser,
        }),
      };

      fetch(config.apiUrl + "users/DeleteDipendente.php", requestOptions)
        .then(()=>props.count())
        .then(()=>handleCloseDelete());
    };

    const generatePassword = () => {
      var length = 14,
        charset =
          "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789?-.,!",
        retVal = "";
      for (var i = 0, n = charset.length; i < length; ++i) {
        retVal += charset.charAt(Math.floor(Math.random() * n));
      }
      var newEditForm = { ...editForm };
      newEditForm.password = retVal;
      setEditForm(newEditForm);
    };

    useEffect(() => {
      GetCity();
      GetCountry();
      GetProvince();
      GetRegione();
    }, [selectedCity, selectedCountry, selectedProvince, selectedRegion]);

    const formik = useFormik({
      initialValues: initialValues,
      enableReinitialize: true,
      validationSchema: validationSchema,
      onSubmit: (values) => {
        const formResult = new FormData();
        formResult.append("pic", file);
        formResult.append("idDipendente", row.idUser);
        formResult.append("idOrganization", user.id_organization);
        formResult.append("idUser", user.id);
        for (let value in values) {
          formResult.append(value, values[value]);
        }
        const requestOptions = {
          headers: {
            Authorization:
              "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
          },
          method: "POST",
          body: formResult,
        };
        fetch(config.apiUrl + "users/EditUser.php", requestOptions)
          .then((response) => response.json())
          .then((data) => {
            setShowEdit(data.result);
            props.handleOpenToast(!showEdit);
            props.setMessage(data[0].message);
            props.setTitle("Modifica profilo terminata");
            setEmailMessage(data[0].emailMessage);
            setEmailAziendaleMessage(data[0].emailAziendaleMessage);
            setPhoneMessage(data[0].phoneMessage);
          })
          .then(()=>props.count())
      },
    });

    return (
      <>
        {/* Delete Modal: BEGIN */}

        <Modal centered show={showDelete} onHide={handleCloseDelete}>
          <Modal.Header closeButton>
            <Modal.Title>Elimina {row.nomeCompleto}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            Sei sicuro di voler eliminare definitivamente l'utente?
          </Modal.Body>
          <Modal.Footer>
            <Button variant="danger" onClick={() => DeleteUser()}>
              Elimina
            </Button>
            <Button variant="primary" onClick={handleCloseDelete}>
              Annulla
            </Button>
          </Modal.Footer>
        </Modal>

        {/* Delete Modal: END */}

        {/* Edit Modal: BEGIN */}

        <Modal centered size="lg" show={showEdit} onHide={handleCloseEdit}>
          <Modal.Header closeButton>
            <Modal.Title>
              Modifica scheda di <b>{row.nomeCompleto}</b>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <form>
              <div className="row">
                <div className="form-group col-6">
                  <label className="col-12">Nome utente</label>
                  <input
                    type="text"
                    name="nome"
                    placeholder="Nome utente..."
                    value={formik.values.nome}
                    {...formik.getFieldProps("nome")}
                    className={`form-control ${getInputClassesForHandle(
                      "nome"
                    )}`}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.nome && formik.errors.nome ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.nome}</div>
                    </div>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12">Cognome utente</label>
                  <input
                    type="text"
                    name="cognome"
                    {...formik.getFieldProps("cognome")}
                    placeholder="Cognome utente..."
                    value={formik.values.cognome}
                    className={`form-control ${getInputClassesForHandle(
                      "cognome"
                    )}`}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.cognome && formik.errors.cognome ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.cognome}
                      </div>
                    </div>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12">Email</label>
                  <input
                    type="email"
                    placeholder="Email..."
                    {...formik.getFieldProps("email")}
                    name="email"
                    value={formik.values.email}
                    className={`form-control ${getInputClassesForHandle(
                      "email"
                    )}`}
                    onClick={() => setEmailMessage(false)}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.email && formik.errors.email ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.email}</div>
                    </div>
                  ) : null}
                  {emailMessage === true ? (
                    <p style={{ color: "red" }}>{message}</p>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12">Email aziendale</label>
                  <input
                    type="email"
                    {...formik.getFieldProps("emailAziendale")}
                    placeholder="Email aziendale..."
                    name="emailAziendale"
                    value={formik.values.emailAziendale}
                    className={`form-control ${getInputClassesForHandle(
                      "emailAziendale"
                    )}`}
                    onClick={() => setEmailAziendaleMessage(false)}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.emailAziendale &&
                  formik.errors.emailAziendale ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.emailAziendale}
                      </div>
                    </div>
                  ) : null}
                  {emailAziendaleMessage === true ? (
                    <p style={{ color: "red" }}>{message}</p>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">Anno di nascita</label>
                  <input
                    type="date"
                    placeholder="Anno di nascita..."
                    name="birthday"
                    value={formik.values.birthday}
                    className="form-control"
                    onChange={formik.handleChange}
                  />
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">
                    Seleziona immagine (.jpg, .png)
                  </label>
                  <div className="custom-file">
                    <Form.File
                      type="file"
                      accept="image/jpeg, image/png"
                      className="custom-file-input"
                      id="customFile"
                      style={{ visibility: "hidden" }}
                      onChange={(e) => setFile(e.target.files[0])}
                    />
                    <label className="custom-file-label" htmlFor="customFile">
                      {file ? file.name : "Seleziona file"}
                    </label>
                  </div>
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">Password</label>
                  <div className="input-group mb-2">
                    <div className="input-group-prepend">
                      <div
                        onClick={() => generatePassword()}
                        className="btn generatePassword input-group-text"
                      >
                        Genera
                      </div>
                    </div>
                    <input
                      type="text"
                      placeholder="Genera nuova password..."
                      value={formik.values.password}
                      className="form-control"
                      style={{ float: "left" }}
                      name="password"
                      onChange={formik.handleChange}
                    />
                  </div>
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">Numero telefono</label>
                  <input
                    type="text"
                    placeholder="Numero telefono..."
                    name="phone"
                    value={formik.values.phone}
                    {...formik.getFieldProps("phone")}
                    className={`form-control ${getInputClassesForHandle(
                      "phone"
                    )}`}
                    onClick={() => setPhoneMessage(false)}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.phone && formik.errors.phone ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.phone}</div>
                    </div>
                  ) : null}
                  {phoneMessage === true ? (
                    <p style={{ color: "red" }}>{message}</p>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">Nazione</label>
                  <select
                    className="form-control"
                    value={selectedCountry}
                    onChange={(e) => {
                      setSelectedCountry(e.target.value);
                    }}
                  >
                    <option>Seleziona nazione</option>
                    {country.map((e, key) => (
                      <option value={e.id} key={key}>
                        {e.country}
                      </option>
                    ))}
                  </select>
                </div>
                {selectedCountry === "110" ? (
                  <>
                    <div className="form-group col-6">
                      <label className="col-12 mt-2">Regione</label>
                      <select
                        className="form-control"
                        value={selectedRegion}
                        onChange={(e) => {
                          setSelectedRegion(e.target.value);
                        }}
                      >
                        <option>Seleziona regione</option>
                        {regione.map((e, key) => (
                          <option value={e.id} key={key}>
                            {e.regione}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="form-group col-6">
                      <label className="col-12 mt-2">Provincia</label>
                      <select
                        className="form-control"
                        value={selectedProvince}
                        onChange={(e) => {
                          setSelectedProvince(e.target.value);
                        }}
                      >
                        <option>Seleziona capoluogo</option>
                        {province.map((e, key) => (
                          <option value={e.id} key={key}>
                            {e.provincia}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="form-group col-6">
                      <label className="col-12 mt-2">Citt</label>
                      <select
                        className="form-control"
                        value={selectedCity}
                        onChange={(e) => {
                          setSelectedCity(e.target.value);
                        }}
                      >
                        <option>Seleziona citt</option>
                        {city.map((e, key) => (
                          <option value={e.id} key={key}>
                            {e.city}
                          </option>
                        ))}
                      </select>
                    </div>
                  </>
                ) : null}
                <div className="form-group col-6">
                  <label className="col-12 mt-2">CAP</label>
                  <input
                    type="text"
                    placeholder="CAP..."
                    name="zip"
                    value={formik.values.zip}
                    {...formik.getFieldProps("zip")}
                    className={`form-control ${getInputClassesForHandle(
                      "zip"
                    )}`}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.zip && formik.errors.zip ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.zip}</div>
                    </div>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">Indirizzo</label>
                  <input
                    type="text"
                    placeholder="Indirizzo..."
                    name="address"
                    value={formik.values.address}
                    className="form-control"
                    onChange={formik.handleChange}
                  />
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">Partita IVA</label>
                  <input
                    type="text"
                    name="vatNumber"
                    placeholder="Partita IVA..."
                    value={formik.values.vatNumber}
                    {...formik.getFieldProps("vatNumber")}
                    className={`form-control ${getInputClassesForHandle(
                      "vatNumber"
                    )}`}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.vatNumber && formik.errors.vatNumber ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.vatNumber}
                      </div>
                    </div>
                  ) : null}
                </div>
                <div className="form-group col-6">
                  <label className="col-12 mt-2">IBAN</label>
                  <input
                    type="text"
                    placeholder="IBAN..."
                    value={formik.values.iban}
                    name="iban"
                    className={`form-control ${getInputClassesForHandle(
                      "iban"
                    )}`}
                    {...formik.getFieldProps("iban")}
                    onChange={formik.handleChange}
                  />
                  {formik.touched.iban && formik.errors.iban ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.iban}</div>
                    </div>
                  ) : null}
                </div>
                <div className="col-6 form-group">
                  <label className="col-12">Ruolo</label>
                  <select
                    onChange={formik.handleChange}
                    value={formik.values.idRole}
                    name="idRole"
                    className="form-control"
                  >
                    <option>Seleziona ruolo</option>
                    {props.roles.map((e, key) => (
                      <option value={e.id} key={key}>
                        {e.role}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="col-6 form-group">
                  <label className="col-12">Dipartimento</label>
                  <select
                    onChange={formik.handleChange}
                    value={formik.values.idDepartment}
                    name="idDepartment"
                    className="form-control"
                  >
                    <option>Seleziona dipartimento</option>
                    {props.departments.map((e, key) => (
                      <option value={e.idDepartment} key={key}>
                        {e.nameDepartment}
                      </option>
                    ))}
                  </select>
                </div>
                {props.customfield !== []
                  ? props.customfield.map((e, key) => {
                      return (
                        <div className="col-6 form-group">
                          <label className="col-12">{e.nomeCampo}</label>
                          <input
                            type="text"
                            className="form-control"
                            key={e.slug}
                            name={e.slug}
                            placeholder={"Inserire " + e.nomeCampo}
                            onChange={formik.handleChange}
                            value={formik.values[e.slug]}
                          />
                        </div>
                      );
                    })
                  : null}
              </div>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={formik.handleSubmit}>
              Salva
            </Button>
            <Button variant="secondary" onClick={handleCloseEdit}>
              Annulla
            </Button>
          </Modal.Footer>
        </Modal>

        {/* Edit Modal: END */}

        {/* Riga dipendente: BEGIN */}
        <div className="row">
          <div className="col-2">
            <img
              alt="Immagine profilo mancante"
              src={row.pic}
              className="rounded mx-auto d-block imagine-thumbnail"
              style={{ maxWidth: "120px" }}
            ></img>
          </div>
          <div className="col-10">
            <div className="mt-5 row">
              <div className="col-6">
                <li>Email: {row.email}</li>
                <li>Telefono: {row.phone}</li>
                {parseInt(row.city) === -1 ? null : <li>Citt: {row.city}</li>}
              </div>
              <div className="col-6">
                <li>Dipartimento: {row.department}</li>
                <li>Ruolo: {row.role}</li>
                <li>Compagnia {row.companyName}</li>
              </div>
            </div>
            <div className="mt-7 col-12">
              <Tooltip placement="top" title="Elimina">
                <Button
                  onClick={() => handleShowDelete()}
                  size="sm"
                  className="mt-2"
                  style={{ float: "right" }}
                  variant="outline-dark"
                >
                  <i className="px-1 far fa-trash-alt"></i>
                </Button>
              </Tooltip>
              <Tooltip placement="top" title="Modifica">
                <Button
                  onClick={() => {
                    handleShowEdit();
                    //setValues(handleCustomField());
                  }}
                  size="sm"
                  className="mr-2 mt-2"
                  style={{ float: "right" }}
                  variant="outline-dark"
                >
                  <i className="fas fa-pen px-1"></i>
                </Button>
              </Tooltip>
            </div>
          </div>
        </div>
        {/* Riga dipendente: END */}
      </>
    );
  };

 const [columns] = useState([
    { name: "idUser", title: "ID" },
    { name: "nome", title: "Nome" },
    { name: "cognome", title: "Cognome" },
    { name: "email", title: "E-mail" },
    { name: "departmentFather", title: "Dipartimento" },
    { name: "department", title: "Sotto dipartimento" },
    { name: "role", title: "Ruolo" },
  ]);

  const [rows, setRows] = useState(props.data);

  useEffect(() => {
    setRows(props.data);
  }, [props.data]);

  const pagingPanelMessages = {
    showAll: "Tutti",
    rowsPerPage: "Colonne per pagina",
    info: "Da {from} a {to} ({count} elementi)",
  };

  const tableMessages = {
    noData: "Nessun dato",
  };

  //page sizing
  const [currentPage, setCurrentPage] = useState(0);
  const [pageSize, setPageSize] = useState(10);
  const [pageSizes] = useState([10, 25, 50, 0]);

  //sorting
  const [sorting, setSorting] = useState([
    { columnName: "nome", direction: "asc" },
  ]);

  //column visibility
  const [defaultHiddenColumnNames] = useState(["idUser"]);

  return (
    <div className="card">
      <Grid rows={rows} columns={columns}>
        <SortingState sorting={sorting} onSortingChange={setSorting} />
        <IntegratedSorting />
        <RowDetailState />
        <FilteringState />
        <IntegratedFiltering />
        <PagingState
          currentPage={currentPage}
          onCurrentPageChange={setCurrentPage}
          pageSize={pageSize}
          onPageSizeChange={setPageSize}
        />
        <IntegratedPaging />
        <Table messages={tableMessages} tableComponent={TableComponent} />
        <TableHeaderRow showSortingControls />
        <TableColumnVisibility
          defaultHiddenColumnNames={defaultHiddenColumnNames}
        />
        <TableRowDetail contentComponent={RowDetail} />
        <Toolbar />
        <ColumnChooser />
        <PagingPanel messages={pagingPanelMessages} pageSizes={pageSizes} />
        <TableFilterRow
          messages={filterRowMessages}
        />
      </Grid>
    </div>
  );
};
