/* eslint-disable default-case */
/* eslint-disable react/jsx-no-undef */
/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from "react";
import  { roles } from "../../../../config/config";
import { ProgressBar, Card, Row } from "react-bootstrap";
import { Skeleton } from "@material-ui/lab";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
/*eslint-disable */

export function StatusBarOfficina(props) {
  const dati = props.dati;
  const [percDay, setPercDay] = useState(0);
  const [percWeek, setPercWeek] = useState(0);
  const [colorDaily, setColorDaily] = useState("red");
  const [colorWeek, setColorWeek] = useState("red");

  const handlepercDay = (minRaggiunti, obiettivo) => {
    setPercDay(Math.round((minRaggiunti / obiettivo) * 100));
  };
  const handlepercWeek = (minRaggiunti, obiettivo) => {
    const newLocal = obiettivo * 5;
    setPercWeek(Math.round((minRaggiunti / newLocal) * 100));
  };

  const ColorDay = () => {
    if (percDay < 25) {
      setColorDaily("#e63946");
    } else if (percDay >= 25 && percDay <= 50) {
      setColorDaily("#f4a261");
    } else if (percDay > 50 && percDay <= 75) {
      setColorDaily("#e9c46a");
    } else if (percDay > 75) {
      setColorDaily("#43aa8b");
    }
  };

  const ColorWeek = () => {
    if (percWeek < 25) {
      setColorWeek("#e63946");
    } else if (percWeek >= 25 && percWeek <= 50) {
      setColorWeek("#f4a261");
    } else if (percWeek > 50 && percWeek <= 75) {
      setColorWeek("#e9c46a");
    } else if (percWeek > 75) {
      setColorWeek("#43aa8b");
    }
  };

  var telefonateOggi = dati.todayCalls;
  var obiettivoGiornaliero = dati.targetGiornaliero;
  var telefonateSettimana = dati.weeklyCalls;
  var obiettivoSettimana = dati.targetSettimanale;
  var obiettivoMinuti = dati.minutiProduttivita;
  var minutiRaggiunti = dati.minutiRaggiunti;
  var minutiSettimanaliProduttivita = dati.minutiSettimanaliProduttivita;

  useEffect(() => {
    handlepercDay(minutiRaggiunti, obiettivoMinuti);
    handlepercWeek(minutiSettimanaliProduttivita, obiettivoMinuti);
    ColorDay();
    ColorWeek();
  }, [props.dati]);

  function chiamateGiorno() {
    var nGiorno = Math.round((100 * telefonateOggi) / obiettivoGiornaliero);
    //setPercentuale(Math.round(100*dati.minutiRaggiunti/dati.minutiProduttivita))
    return nGiorno;
  }

  function chiamateSettimana() {
    var nSettimana = Math.round(
      (100 * telefonateSettimana) / obiettivoSettimana
    );
    return nSettimana;
  }

  function formatHours(hour) {
    var num = hour;
    var hours = num / 60;
    var rhours = Math.floor(hours);
    var minutes = (hours - rhours) * 60;
    var rminutes = Math.round(minutes);

    if (rminutes > 1 || rminutes === 0) {
      var textMin = " MINUTI";
    } else {
      textMin = " MINUTO";
    }

    if (rhours > 1) {
      var textHours = " ORE";
    } else {
      textHours = " ORA";
    }

    if (rhours === 0) {
      return rminutes + textMin;
    } else {
      return (
        rhours + textHours + (rminutes > 1 ? " e " + rminutes + textMin : "")
      );
    }
  }

  return (
    <>
      <Card>
        {parseInt(props.user.id_role) === roles.admin ||
        parseInt(props.user.id_role) === roles.hr ? (
          <Card.Header>
            <div className="col-6">
              <select
                onChange={(e) => {
                  props.setDipendente(e.target.value);
                }}
                value={props.dipendente}
                className="form-control mt-5"
              >
                <option>Seleziona dipendente</option>
                {props.users.map((e, key) => {
                  return (
                    <option key={key} value={e.idDipendente}>
                      {e.fullname}
                    </option>
                  );
                })}
              </select>{" "}
            </div>
          </Card.Header>
        ) : (
          <Card.Header>
            <h6 className="card-title-h3 col-6">PROGRESSI</h6>
          </Card.Header>
        )}
        <Card.Body>
          <Row>
            <div className="col-6 mt-8 my-8">
              <div className="col-12 mt-10">
                <label className="col-12">
                  Chiamate effettuate ieri:{" "}
                  <strong>
                    {!props.loading
                      ? telefonateOggi + "/" + obiettivoGiornaliero
                      : "Caricamento..."}
                  </strong>
                </label>
                {!props.loading ? (
                  <ProgressBar
                    animated
                    variant="success"
                    now={chiamateGiorno()}
                    label={`${chiamateGiorno()}%`}
                  />
                ) : (
                  <Skeleton animation="wave" width={540} />
                )}
              </div>
              <div className="col-12 mt-20">
                <label className="col-12">
                  Chiamate effettuate questa settimana:{" "}
                  <strong>
                    {!props.loading
                      ? telefonateSettimana + "/" + obiettivoSettimana
                      : "Caricamento..."}
                  </strong>
                </label>
                {!props.loading ? (
                  <ProgressBar
                    animated
                    variant="info"
                    now={chiamateSettimana()}
                    label={`${chiamateSettimana()}%`}
                  />
                ) : (
                  <Skeleton animation="wave" width={540} />
                )}
              </div>
            </div>
            <div className="col-3 mt-8 my-8">
              <div className="col-12">
                <label className="col-12 text-center">
                  <b>Riepilogo produttivit giorno precedente:</b> <br />
                  {formatHours(minutiRaggiunti)} SU{" "}
                  {formatHours(obiettivoMinuti)}
                </label>
                <div
                  className="d-flex ml-10"
                  style={{ width: 150, height: 150 }}
                >
                  {!props.loading ? (
                    <CircularProgressbar
                      styles={buildStyles({
                        pathColor: colorDaily,
                        textColor: colorDaily,
                      })}
                      className="ml-4"
                      value={percDay}
                      text={`${percDay}%`}
                    />
                  ) : (
                    <Skeleton
                      className="ml-4"
                      variant="circle"
                      animation="wave"
                      width={140}
                      height={140}
                    />
                  )}
                </div>
              </div>
            </div>
            <div className="col-3 mt-8 my-8">
              <div className="col-12">
                <label className="col-12 text-center">
                  <b>Riepilogo produttivit giorno precedente:</b> <br />
                  {formatHours(minutiSettimanaliProduttivita)} SU{" "}
                  {formatHours(obiettivoMinuti * 5)}
                </label>
                <div
                  className="d-flex ml-10"
                  style={{ width: 150, height: 150 }}
                >
                  {!props.loading ? (
                    <CircularProgressbar
                      styles={buildStyles({
                        pathColor: colorWeek,
                        textColor: colorWeek,
                      })}
                      className="ml-4"
                      value={percWeek}
                      text={`${percWeek}%`}
                    />
                  ) : (
                    <Skeleton
                      className="ml-4"
                      variant="circle"
                      animation="wave"
                      width={140}
                      height={140}
                    />
                  )}
                </div>
              </div>
            </div>
          </Row>
        </Card.Body>
      </Card>
    </>
  );
}
