/* eslint-disable no-restricted-imports */
/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React from "react";
import { useSelector } from "react-redux";
import it from "date-fns/locale/it";
import { DateRangePicker } from "react-date-range";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import {
  defaultStaticRanges,
  defaultInputRanges,
} from "react-date-range/dist/defaultRanges";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Typography,
} from "@material-ui/core";

import { scaleBand } from "@devexpress/dx-chart-core";
import { ArgumentScale } from "@devexpress/dx-react-chart";
import "react-date-range/dist/styles.css"; // main style file
import { line, curveStep } from "d3-shape";
import "react-date-range/dist/theme/default.css"; // theme css file
import { roles } from "../../../../config/config";
import {
  Chart,
  ArgumentAxis,
  ValueAxis,
  BarSeries,
  LineSeries,
  Title,
  Legend,
} from "@devexpress/dx-react-chart-material-ui";
import { Card } from "react-bootstrap";
import { ValueScale, Animation, Stack } from "@devexpress/dx-react-chart";
import { Skeleton } from "@material-ui/lab";

export function ChartOfficina(props) {
  const { user } = useSelector((state) => state.auth);

  const Line = (props) => (
    <LineSeries.Path
      {...props}
      path={line()
        .x(({ arg }) => arg)
        .y(({ val }) => val)
        .curve(curveStep)}
    />
  );

  const Root = (props) => (
    <Legend.Root {...props} className="m-auto flex-row" />
  );

  const Marker = (props) => {
    const { className, color } = props;
    return (
      <svg className={className} fill={color} width="10" height="10">
        <rect x={0} y={0} width={100} height={100} />
      </svg>
    );
  };

  const format = () => (tick) => tick;

  const staticRangesLabels = {
    Today: "Oggi",
    Yesterday: "Ieri",
    "This Week": "Questa settimana",
    "Last Week": "Settimana scorsa",
    "This Month": "Questo mese",
    "Last Month": "Mese scorso",
  };

  const inputRangesLabels = {
    "days up to today": "giorni da oggi",
    "days starting today": "giorni a partire da oggi",
  };

  function translateRange(dictionary) {
    return (item) =>
      dictionary[item.label]
        ? { ...item, label: dictionary[item.label] }
        : item;
  }

  const itStaticRanges = defaultStaticRanges.map(
    translateRange(staticRangesLabels)
  );
  const itInputRanges = defaultInputRanges.map(
    translateRange(inputRangesLabels)
  );

  return (
    <div className="row">
      <div className="col-12 mt-2 mb-2"></div>

      <div className="col-12">
        <>
          <Card>
            <Card.Header>
              <h6 className="card-title-h3">
                GRAFICO RIEPILOGO - SELEZIONA INTERVALLO DATE
              </h6>
            </Card.Header>
            <Card.Body>
              <Accordion
                style={{ backgroundColor: "#ffff" }}
                className="mt-4 mb-4 col-7 mx-auto"
              >
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>
                    Seleziona intervallo{" "}
                    {parseInt(user.id_role) === roles.admin ||
                    parseInt(user.id_role) === roles.hr
                      ? "- Dipendente"
                      : null}
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <div className="row">
                    {parseInt(user.id_role) === roles.admin ||
                    parseInt(user.id_role) === roles.hr ? (
                      <select
                        value={props.dipendente}
                        onChange={(e) => {
                          props.setDipendente(e.target.value);
                        }}
                        className="form-control col-12"
                      >
                        <option>Seleziona dipendente</option>
                        {props.users.map((e, key) => {
                          return (
                            <option key={key} value={e.idDipendente}>
                              {e.fullname}
                            </option>
                          );
                        })}
                      </select>
                    ) : null}
                    <div>
                      <DateRangePicker
                        staticRanges={itStaticRanges}
                        inputRanges={itInputRanges}
                        className="col-12 mt-3"
                        locale={it}
                        ranges={[props.selectionRange]}
                        onChange={props.handleSelect}
                      />
                    </div>
                  </div>
                </AccordionDetails>
              </Accordion>
              {!props.loading ? (
                <Chart
                  data={
                    props.target !== []
                      ? props.target
                      : [{ month: "NO DATA", minuti: 0 }]
                  }
                >
                  <ValueScale name="minuti" />
                  <ArgumentScale factory={scaleBand} />
                  <ArgumentAxis tickFormat={format} />
                  <ValueAxis scaleName="minuti" showLine showTicks />
                  <BarSeries
                    name="Minuti raggiunti"
                    valueField="minuti"
                    argumentField="month"
                    scaleName="minuti"
                    color="#0f4c5c"
                  />
                  <BarSeries
                    name="Numero lavori"
                    valueField="nrWorks"
                    argumentField="month"
                    scaleName="minuti"
                    color="#ff9f1c"
                  />
                  <LineSeries
                    name="()Obiettivo minuti"
                    valueField="obiettivo"
                    argumentField="month"
                    scaleName="minuti"
                    color="#ffba08"
                    seriesComponent={Line}
                  />
                  <LineSeries
                    name="()Target giornaliero"
                    valueField="target"
                    argumentField="month"
                    scaleName="minuti"
                    color="#e71d36"
                    seriesComponent={Line}
                  />
                  <Animation />
                  <Legend
                    position="right"
                    rootComponent={Root}
                    markerComponent={Marker}
                  />
                  <Title text="Grafico officina" />
                  <Stack />
                </Chart>
              ) : (
                <div className="row ml-4">
                  <div className="col-2">
                    <Skeleton animation="wave" height={500} width={50} />
                  </div>
                  <div className="col-2">
                    <Skeleton animation="wave" height={500} width={50} />
                  </div>
                  <div className="col-2">
                    <Skeleton animation="wave" height={500} width={50} />
                  </div>
                  <div className="col-2">
                    <Skeleton animation="wave" height={500} width={50} />
                  </div>
                  <div className="col-2">
                    <Skeleton animation="wave" height={500} width={50} />
                  </div>
                  <div className="col-2">
                    <Skeleton animation="wave" height={500} width={50} />
                  </div>
                </div>
              )}
            </Card.Body>
          </Card>
        </>
      </div>
    </div>
  );
}
