import React, { Suspense } from "react";
import { Redirect, Switch } from "react-router-dom";
import { PaginaDipendente } from "./PaginaDipendente";
import { LayoutSplashScreen, ContentRoute } from "../../_metronic/layout";
import { roles } from "../../config/config";
import { useSelector } from "react-redux";

export function Obiettivi() {
  const { user } = useSelector((state) => state.auth);
  var path =
    parseInt(user.id_role) === roles.admin
      ? "/obiettivi/" +
        user.moduleName.replace(/\s+/g, "-").trim().toLowerCase()
      : "/obiettivi/" +
        user.nameDepartment.replace(/\s+/g, "-").trim().toLowerCase();
  return (
    <Suspense fallback={<LayoutSplashScreen />}>
      <Switch>
        {
          /* Redirect from eCommerce root URL to /customers */

          <Redirect exact={true} from="/obiettivi" to={path} />
        }
        <ContentRoute path={path} component={PaginaDipendente} />
      </Switch>
    </Suspense>
  );
}
