/* eslint-disable no-restricted-imports */
import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from "formik";
import config from "../../config/config";
import * as Yup from "yup";
import { Card, Form, Button } from "react-bootstrap";

export function GestisciRuoli() {
  /*eslint-disable */
  useEffect(() => {
    fetchDataAllRoles();
  }, []);

  const [selectRoleName, setSelectedRoleName] = useState("");
  const [selectedRoleId, SetSelectedRoleId] = useState(0);
  const [deletable, setDeletable] = useState(true);
  const [switches, setSwitches] = useState([]);
  const [success, setSuccess] = useState(false);
  const [message, setMessage] = useState("");
  const [create, setCreate] = useState(false);

  const [listRoles, setListRoles] = useState([]);

  const { user } = useSelector((state) => state.auth);

  if (user[0].gestioneRuoli === false) {
    window.location.href = "/dashboard";
  }

  const useStyles = makeStyles((theme) => ({
    rroot: {
      width: "100%",
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
  }));

  const classes = useStyles();

  const fetchDataAllRoles = async () => {
    const requesstarttions = {
      headers: {
        Authorization:
          "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
      },
      method: "POST",
      body: JSON.stringify({
        idUtente: user.id,
        idOrganization: user.id_organization,
      }),
    };

    const rawData = await fetch(
      config.apiUrl + "users/GetAllRoles.php?id_user=0",
      requesstarttions
    );
    const data = await rawData.json();
    setListRoles(data);
  };

  //validation schema
  const schema = Yup.object().shape({
    role: Yup.string().required("Selezionare un ruolo"),
  });

  //Form per gestire ruoli

  const form = useFormik({
    initialValues: {
      idUtente: user.id,
      idOrganization: user.id_organization,
      role: selectedRoleId,
      dashboard: switches.dashboard,
      presenze: switches.presenze,
      bustePaga: switches.bustePaga,
      pannelloAdmin: switches.pannelloAdmin,
      gestioneGruppi: switches.gestioneGruppi,
      gestioneRuoli: switches.gestioneRuoli,
      impostazioni: switches.impostazioni,
      documenti: switches.documenti,
      dipendenti: switches.dipendenti,
    },
    enableReinitialize: true,
    validationSchema: schema,
    onSubmit: (values) => {
      const requesstarttions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({
          values,
        }),
      };

      fetch(config.apiUrl + "users/UpdateRole.php", requesstarttions)
        .then((response) => response.json())
        .then(setSuccess(true))
        .then(fetchDataAllRoles());
    },
  });

  //Funzione per rendere solo la prima lettera maiuscola
  function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  //Funzion per gestire lo stato e la selezione dei ruoli
  function handlingNameRole(input) {
    listRoles.map((e, key) => {
      if (e.id === input) {
        var nameRole = e.role.toLowerCase();
        setSelectedRoleName(capitalizeFirstLetter(nameRole));
        setDeletable(e.deletable);
        setSwitches(e[0]);
        setSuccess(false);
      }
    });
  }

  return (
    <div className="col-12">
      <Card>
        {/*form: gestisci ruolo <begin>*/}
        <Card.Header style={{ backgroundColor: "#264653" }}>
          <h6
            className={classes.heading}
          >
            PERMESSI RUOLI
          </h6>
        </Card.Header>
        <Form className="mt-3">
          <div className="row">
            {/* begin: Ruoli */}
            <div className="col-12">
              <Form.Group className="col-12">
                <select
                  onClick={(e) => {
                    handlingNameRole(form.values.role);
                  }}
                  className="form-control"
                  name="role"
                  {...form.getFieldProps("role")}
                >
                  <option>Seleziona il ruolo</option>
                  {listRoles.map((e, key) => {
                    return (
                      <option key={key} value={e.id}>
                        {e.role}
                      </option>
                    );
                  })}
                </select>

                {form.touched.role && form.errors.role ? (
                  <div className="fv-plugins-message-container">
                    <div className="fv-help-block">{form.errors.role}</div>
                  </div>
                ) : null}
              </Form.Group>
              {/* end: Ruoli */}

              {/* begin: switch */}
              <Form.Row>
                <Form.Group className="col-6">
                  <label className="checkbox col-xl-12 col-lg-12">
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.dashboard}
                      onChange={form.handleChange}
                      name="dashboard"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Dashboard
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.pannelloAdmin}
                      onChange={form.handleChange}
                      name="pannelloAdmin"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Pannello admin
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.gestioneGruppi}
                      onChange={form.handleChange}
                      name="gestioneGruppi"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Gestione dipartimenti
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.gestioneRuoli}
                      onChange={form.handleChange}
                      name="gestioneRuoli"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Gestione ruoli
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.impostazioni}
                      onChange={form.handleChange}
                      name="impostazioni"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Impostazioni
                  </label>
                </Form.Group>
                <Form.Group className="col-6">
                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.presenze}
                      onChange={form.handleChange}
                      name="presenze"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Presenze
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.bustePaga}
                      onChange={form.handleChange}
                      name="bustePaga"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Buste paga
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.documenti}
                      onChange={form.handleChange}
                      name="documenti"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Documenti
                  </label>

                  <label
                    className="checkbox col-xl-12 col-lg-12"
                    style={{ marginTop: "7%" }}
                  >
                    <input
                      type="checkbox"
                      color="primary"
                      checked={form.values.dipendenti}
                      onChange={form.handleChange}
                      name="dipendenti"
                    />
                    <span style={{ marginRight: "5px" }}></span>
                    Dipendenti
                  </label>
                </Form.Group>
              </Form.Row>
              {/* end: switch */}

              <Button
                color="primary"
                onClick={form.handleSubmit}
                className = "mx-4 my-4 float-left"
              >
                Salva modifiche
              </Button>
              {success === true ? (
                <p style={{ color: "green", fontSize: "11px", float: "right" }}>
                  Ruolo modificato con successo
                </p>
              ) : null}
            </div>
          </div>
        </Form>

        {/*gestisci ruoli: Modifica/elimina ruolo <end>*/}
      </Card>
    </div>
  );
}
