import React from 'react';
import {Modal, Button} from 'react-bootstrap'
import { useFormik } from 'formik';
import * as Yup from 'yup';
import {useSelector} from 'react-redux';
import { parse, isDate } from "date-fns";
import config from '../../../../config/config';

export function ModalCustomerCare(props) {
    const {user} = useSelector((state) => state.auth);

    function parseDateString(value, originalValue) {
        const parsedDate = isDate(originalValue)
          ? originalValue
          : parse(originalValue, "yyyy-MM-dd", new Date());
      
        return parsedDate;
    }

    const getInputClassesForHandle = (fieldname) => {
        if (formik.touched[fieldname] && formik.errors[fieldname]) {
          return "is-invalid";
        }
    
        if (formik.touched[fieldname] && !formik.errors[fieldname]) {
          return "is-valid";
        }
        return ""
    }

    const schema = Yup.object().shape({
       tipoChiamata : Yup.string()
       .required("Indicare il tipo di chiamata"),
       dataRicevuta: Yup.date().transform(parseDateString),
       oraRicevuta: Yup.string()
       .required("Inserire l'orario di ricevuta"),
       durata: Yup.string()
       .required("Inserire la durata della chiamata"),
       nrDialed: Yup.string()
       .required("Inserire numero dialed") 
       .matches(/^[0-9]+$/, "Il numero dialed non pu contenere caratteri"),
       ringTime: Yup.string().notRequired("Inserire il tempo di squillo"),
       esitoChiamata: Yup.string().required("Inserire l'esito chiamata"),
       numeroChiamante: Yup.string()
       .required("Inserire il numero del cliente")
       .matches(/^[0-9]+$/, "Il numero di telefono non pu contenere caratteri"),
       codiceSoggetto: Yup.string()
       .required("Inserire il codice soggetto")
       .matches(/^[0-9]+$/, "Il codice soggetto non pu contenere caratteri"),
       nomeSoggetto: Yup.string().required("Inserire il nome del cliente"),
    })

    const formik = useFormik({
        initialValues: {
            idUtente : user.id,
            tipoChiamata: "",
            dataRicevuta: "",
            oraRicevuta: "",
            durata: "00:00:00",
            nrDialed: "",
            ringTime: "00:00:00",
            esitoChiamata: "",
            numeroChiamante: "",
            codiceSoggetto: "",
            nomeSoggetto: "",
            
        },
        enableReinitialize: true,
        validationSchema: schema,
        onSubmit: (values) => {
            const options = {
                headers: {
                  'Authorization': "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
                },
                method: 'POST',
                body: JSON.stringify({
                  values
                })
            };
            fetch(config.apiUrl + 'obiettivi/AddTicket.php', options)
            .then(response=>response.json())
            //.then(window.top.location.reload())
        }
    })
    
    
    return(
        <>
        <Modal
        show={props.show}
        onHide={props.handleClose}
        backdrop="static"
        size="xl"
        >
        <Modal.Header style={{backgroundColor:"#caf0f8"}} closeButton>
        <Modal.Title>
            Aggiungi nuova chiamata
        </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <form onSubmit = {formik.handleSubmit}>
                <div className = "form-group row">
                    <div className = "col-lg-6 col-xl-6">
                        
                        {/* begin: tipo chiamata */}
                        <div className="col-lg-12 col-xl-12">
                            <label className="col-xl-12 col-lg-12 col-form-label">
                            Selezionare tipo chiamata
                            </label>
                            <div className="input-group input-group-xl input-group-solid" style={{marginBottom:"1.75rem"}}>
                            <select
                                className="form-control form-control-xl form-control-solid"
                                name="tipoChiamata"
                                {...formik.getFieldProps("tipoChiamata")}
                            >
                                <option>Tipo chiamata</option>
                                <option value="INBOUND">Inbound</option>
                                <option value="OUTBOUND">Outbound</option>
                            </select>
                            </div>
                        </div>
                        {formik.touched.tipoChiamata && formik.errors.tipoChiamata ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.tipoChiamata}</div>
                            </div>
                        ) : null} 
                        {/* end: tipo chiamata */}  
                        
                        {/* begin: data ricevuta */}
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare data ricevuta (DD-MM-YYYY)
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{ maxWidth:"98%"}}>
                        <input
                            placeholder="Data ricevuta"
                            type="date"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "dataRicevuta"
                            )}`}
                            value={formik.values.dataRicevuta}
                            name="dataRicevuta"
                            {...formik.getFieldProps("dataRicevuta")}
                        />
                        {formik.touched.dataRicevuta && formik.errors.dataRicevuta ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.dataRicevuta}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: data ricevuta */}  
                        
                        {/* begin: ora ricevuta */}
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare ora ricevuta in questo formato (HH:MM)
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{maxWidth:"98%"}}>
                        <input
                            required    
                            placeholder="Ora ricevuta"
                            type="time"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "oraRicevuta"
                            )}`}
                            value={formik.values.oraRicevuta}
                            name="oraRicevuta"
                            {...formik.getFieldProps("oraRicevuta")}
                        />
                        {formik.touched.oraRicevuta && formik.errors.oraRicevuta ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.oraRicevuta}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: ora ricevuta */}  
                        
                        {/* begin: durata */}
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare durata chiamata in questo formato (HH:MM:SS)
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{ maxWidth:"98%"}}>
                        <input
                            required
                            placeholder="Durata chiamata"
                            type="time"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "durata"
                            )}`}
                            value={formik.values.durata}
                            name="durata"
                            {...formik.getFieldProps("durata")}
                        />
                        {formik.touched.durata && formik.errors.durata ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.durata}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: durata */}  
                        
                        {/* begin: nr dialed */}
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare numero dialed
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{ maxWidth:"98%"}}>
                        <input
                            placeholder="Numero dialed"
                            type="nrDialed"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "nrDialed"
                            )}`}
                            value={formik.values.nrDialed}
                            name="nrDialed"
                            {...formik.getFieldProps("nrDialed")}
                        />
                        {formik.touched.nrDialed && formik.errors.nrDialed ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.nrDialed}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: nr dialed */}

                        {/* begin: ring time */}
                        
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare durata dello squillo telefono (HH:MM:SS)
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{marginTop:"1.75rem", maxWidth:"98%"}}>
                        <input
                            disabled={formik.values.tipoChiamata === "OUTBOUND" ? true : false}
                            required
                            placeholder="Ring time"
                            type="time"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "ringTime"
                            )}`}
                            value={formik.values.tipoChiamata === "OUTBOUND" ? "00:00:00" : formik.values.ringTime}
                            name="ringTime"
                            {...formik.getFieldProps("ringTime")}
                        />
                        {formik.touched.ringTime && formik.errors.ringTime ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.ringTime}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: ring time */}   

                    </div>

                    {/* begin: esito chiamata */}
                    
                    <div className = "col-lg-6 col-xl-6">
                        <div className="col-lg-12 col-xl-12" style={{marginBottom:"1.75rem"}}>
                            <label className="col-xl-12 col-lg-12 col-form-label">
                                Selezionare esito chiamata
                            </label>
                            <div className="input-group input-group-xl input-group-solid">
                            <select
                                className="form-control form-control-xl form-control-solid"
                                name="esitoChiamata"
                                {...formik.getFieldProps("esitoChiamata")}
                            >
                                <option>Esito chiamata</option>
                                <option value="RISPOSTA">Risposta</option>
                                <option value="SENZA RISPOSTA">Senza risposta</option>
                                <option value="ANOMALIA">Anomalia</option>
                            </select>
                            </div>
                        </div>
                        {formik.touched.esitoChiamata && formik.errors.esitoChiamata ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.esitoChiamata}</div>
                            </div>
                        ) : null}
                    {/* end: esito chiamata */}

                    {/* begin: numero chiamante */}
                    <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare numero chiamante
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{ maxWidth:"98%"}}>
                        <input
                            placeholder="Numero chiamante"
                            type="numeroChiamante"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "numeroChiamante"
                            )}`}
                            value={formik.values.numeroChiamante}
                            name="numeroChiamante"
                            {...formik.getFieldProps("numeroChiamante")}
                        />
                        {formik.touched.numeroChiamante && formik.errors.numeroChiamante ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.numeroChiamante}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: numero chiamante */}

                        {/* begin: codice soggetto */}
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare codice soggetto
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{ maxWidth:"98%"}}>
                        <input
                            placeholder="Codice soggetto"
                            type="codiceSoggetto"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "codiceSoggetto"
                            )}`}
                            value={formik.values.codiceSoggetto}
                            name="codiceSoggetto"
                            {...formik.getFieldProps("codiceSoggetto")}
                        />
                        {formik.touched.codiceSoggetto && formik.errors.codiceSoggetto ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.codiceSoggetto}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: codice soggetto */}

                        {/* begin: nome soggetto */}
                        <label className="col-xl-12 col-lg-12 col-form-label">
                            Indicare nome soggetto
                        </label>
                        <div className="form-group fv-plugins-icon-container" style={{ maxWidth:"98%"}}>
                        <input
                            placeholder="Nome soggetto"
                            type="nomeSoggetto"
                            className={`form-control form-control-solid h-auto py-5 px-6 ${getInputClassesForHandle(
                            "nomeSoggetto"
                            )}`}
                            value={formik.values.nomeSoggetto}
                            name="nomeSoggetto"
                            {...formik.getFieldProps("nomeSoggetto")}
                        />
                        {formik.touched.nomeSoggetto && formik.errors.nomeSoggetto ? (
                            <div className="fv-plugins-message-container">
                            <div className="fv-help-block">{formik.errors.nomeSoggetto}</div>
                            </div>
                        ) : null}
                        </div>
                        {/* end: nome soggetto */}

                         

                    </div>
                </div>
            </form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant="secondary" onClick={props.handleClose}>
                Annulla
            </Button>
            <Button variant="primary" onClick={formik.handleSubmit}>
                Salva
            </Button>
        </Modal.Footer>    
        </Modal>
        </>

    )
}