/* eslint-disable no-restricted-imports */
import React, { useEffect, useState } from "react";
import { TabellaCustomerCare } from "../../_metronic/_partials/widgets/advance-tables/TabellaCustomerCare";
import { TabellaOfficina } from "../../_metronic/_partials/widgets/advance-tables/TabellaOfficina";
import { StatusBarCustomerCareDaily } from "../../_metronic/_partials/widgets/mixed/StatusBarCustomerCareDaily";
import { StatusBarOfficina } from "../../_metronic/_partials/widgets/mixed/StatusBarOfficina";
import { ChartDipendenti } from "../../_metronic/_partials/widgets/mixed/ChartDipendenti";
import { ChartOfficina } from "../../_metronic/_partials/widgets/mixed/ChartOfficina";
import { ChartVenditori } from "../../_metronic/_partials/widgets/mixed/ChartVenditori";
import { ModalCustomerCare } from "../../_metronic/_partials/widgets/mixed/ModalCustomerCare";
import { Card } from "react-bootstrap";
import { useSelector } from "react-redux";
import config from "../../config/config";
import { roles, modules } from "../../config/config";
import { TabellaVenditori } from "../../_metronic/_partials/widgets/advance-tables/TabellaVenditori";
import { makeStyles } from "@material-ui/core";
import { TestDrive } from "../../_metronic/_partials/widgets/advance-tables/TestDrive";

export function PaginaDipendente(props) {
  /*eslint-disable */
  const useStyles = makeStyles((theme) => ({
    root: {
      width: "100%",
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      flexBasis: "33.33%",
      flexShrink: 0,
    },
    secondaryHeading: {
      fontSize: theme.typography.pxToRem(15),
      color: theme.palette.text.secondary,
    },
  }));

  const classes = useStyles();

  //Stato per gestione visione admin o supervisor
  const [dipendente, setDipendente] = useState();
  const [officinaUsers, setOfficinaUsers] = useState([]);
  const [customerCareUsers, setCustomerCareUsers] = useState([]);
  const [venditoriUsers, setVenditoriUsers] = useState([]);

  //Stato per intervallo di mesi/giorni/anno
  const [interval, setInterval] = useState("ultimo mese");

  //Dipartimento selezionato
  const [selectDepartment, setSelectDepartment] = useState("customerCare");

  //Stato per salvare i dati di compilazione dei grafici
  const [tabellaCc, setTabellaCc] = useState([]);
  const [tabellaOfficina, setTabellaOfficina] = useState([]);
  const [tabellaContratti, setTabellaContratti] = useState([]);
  const [ticket, setTicket] = useState([]);
  const [target, setTarget] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loadingDati, setLoadingDati] = useState(false);
  const [dati, setDati] = useState([]);
  const [sellChart, setSellChart] = useState([]);

  //stato per intervallo date
  const [startDate, setStartDate] = useState(
    new Date(new Date().setFullYear(new Date().getFullYear() - 1))
  );
  const [endDate, setEndDate] = useState(new Date());
  const selectionRange = {
    startDate: startDate,
    endDate: endDate,
    key: "selection",
  };
  const handleSelect = (ranges) => {
    setStartDate(ranges.selection.startDate);
    setEndDate(ranges.selection.endDate);
  };

  const [show, setShow] = useState(false);
  const [count, setCount] = useState(0);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const { user } = useSelector((state) => state.auth);

  const options = {
    headers: {
      Authorization:
        "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
    },
    method: "POST",
    body: JSON.stringify({
      idUtente: user.id,
      idDepartment: user.idDepartment,
      idOrganization: user.id_organization,
      idRole: user.id_role,
    }),
  };

  async function GetCustomerCareUsers() {
    const rawData = await fetch(
      config.apiUrl + "users/GetCustomerCareUsers.php",
      options
    );
    const data = await rawData.json();
    setCustomerCareUsers(data);
  }

  async function GetOfficinaUsers() {
    const rawData = await fetch(
      config.apiUrl + "users/GetOfficinaUsers.php",
      options
    );
    const data = await rawData.json();
    setOfficinaUsers(data);
  }

  const GetVenditoriUsers = () => {
    fetch(config.apiUrl + "users/GetVenditoriUsers.php", options)
      .then((response) => response.json())
      .then((data) => setVenditoriUsers(data));
  };

  /* {BEGIN ADMIN SETTINGS} */

  const adminOption = {
    headers: {
      Authorization:
        "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
    },
    method: "POST",
    body: JSON.stringify({
      idDipendente: dipendente,
      idRuolo: user.id_role,
      idOrganization: user.id_organization,
      authToken: user.authToken,
      supervisor: true,
      idUser: user.id,
      start: startDate,
      end: endDate,
    }),
  };

  async function GetAllTicketSupervisor() {
    setLoading(true);
    fetch(config.apiUrl + "obiettivi/GetAllTicketSupervisor.php", adminOption)
      .then((response) => response.json())
      .then((data) => setTabellaCc(data))
      .then(() => setLoading(false));
  }

  async function GetAllWorksSupervisor() {
    setLoading(true);
    fetch(config.apiUrl + "obiettivi/GetAllWorksSupervisor.php", adminOption)
      .then((response) => response.json())
      .then((data) => setTabellaOfficina(data))
      .then(() => setLoading(false));
  }

  async function GetTicketChartSupervisor() {
    setLoadingDati(true);
    fetch(config.apiUrl + "obiettivi/GetTicketChartSupervisor.php", adminOption)
      .then((response) => response.json())
      .then((data) => setTicket(data))
      .then(() => setLoadingDati(false));
  }

  async function GetMinuteOfficinaSupervisor() {
    setLoading(true);
    fetch(
      config.apiUrl + "obiettivi/GetMinuteOfficinaSupervisor.php",
      adminOption
    )
      .then((response) => response.json())
      .then((data) => setTarget(data))
      .then(() => setLoading(false));
  }

  async function CalculateTargetSupervisor() {
    setLoadingDati(true);
    fetch(
      config.apiUrl + "obiettivi/CalculateTargetSupervisor.php",
      adminOption
    )
      .then((response) => response.json())
      .then((data) => setDati(data))
      .then(() => setLoadingDati(false));
  }

  async function CalculateOfficinaTargetSupervisor() {
    setLoadingDati(true);
    fetch(
      config.apiUrl + "obiettivi/CalculateOfficinaTargetSupervisor.php",
      adminOption
    )
      .then((response) => response.json())
      .then((data) => setDati(data))
      .then(() => setLoadingDati(false));
  }

  const CalculateGraficoVenditoriSupervisor = () => {
    setLoading(true);
    fetch(
      config.apiUrl + "obiettivi/CalculateGraficoVenditoriEmployee.php",
      adminOption
    )
      .then((response) => response.json())
      .then((data) => setSellChart(data))
      .then(() => setLoading(false));
  };

  const GetAllContrattiSupervisor = () => {
    setLoading(true);
    fetch(config.apiUrl + "obiettivi/GetAllContratti.php", adminOption)
      .then((response) => response.json())
      .then((data) => setTabellaContratti(data))
      .then(() => setLoading(false));
  };

  /* {END ADMIN SETTINGS} */

  /* {BEGIN EMPLOYEE SETTINGS} */

  const employeeOptions = {
    headers: {
      Authorization:
        "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
    },
    method: "POST",
    body: JSON.stringify({
      idUser: user.id,
      idRuolo: user.id_role,
      idDepartment: user.idDepartment,
      authToken: user.authToken,
      supervisor: false,
      start: startDate,
      end: endDate,
    }),
  };

  async function GetAllTicketEmployee() {
    setLoading(true);
    fetch(config.apiUrl + "obiettivi/GetAllTicketEmployee.php", employeeOptions)
      .then((response) => response.json())
      .then((data) => setTabellaCc(data))
      .then(() => setLoading(false));
  }

  async function GetAllWorksEmployee() {
    setLoading(true);
    fetch(config.apiUrl + "obiettivi/GetAllWorksEmployee.php", employeeOptions)
      .then((response) => response.json())
      .then((data) => setTabellaOfficina(data))
      .then(() => setLoading(false));
  }

  async function GetNumberCallsTest() {
    setLoading(true);
    fetch(
      config.apiUrl + "obiettivi/GetNumberCallsChartEmployee.php",
      employeeOptions
    )
      .then((response) => response.json())
      .then((data) => setTicket(data))
      .then(() => setLoading(false));
  }

  async function GetNumberWorksEmployee() {
    setLoadingDati(true);
    fetch(
      config.apiUrl + "obiettivi/GetNumberWorksEmployee.php",
      employeeOptions
    )
      .then((response) => response.json())
      .then((data) => setTarget(data))
      .then(() => setLoadingDati(false));
  }

  async function CalculateTargetEmployee() {
    setLoadingDati(true);
    fetch(
      config.apiUrl + "obiettivi/CalculateTargetEmployee.php",
      employeeOptions
    )
      .then((response) => response.json())
      .then((data) => setDati(data))
      .then(() => setLoadingDati(false));
  }

  async function CalculateOfficinaTargetEmployee() {
    setLoadingDati(true);
    fetch(
      config.apiUrl + "obiettivi/CalculateOfficinaTargetEmployee.php",
      employeeOptions
    )
      .then((response) => response.json())
      .then((data) => setDati(data))
      .then(() => setLoadingDati(false));
  }

  const GetAllContrattiEmployee = () => {
    setLoading(true);
    fetch(config.apiUrl + "obiettivi/GetAllContratti.php", adminOption)
      .then((response) => response.json())
      .then((data) => setTabellaContratti(data))
      .then(() => setLoading(false));
  };

  const CalculateGraficoVenditoriEmployee = () => {
    setLoading(true);
    fetch(
      config.apiUrl + "obiettivi/CalculateGraficoVenditoriEmployee.php",
      employeeOptions
    )
      .then((response) => response.json())
      .then((data) => setSellChart(data))
      .then(() => setLoading(false));
  };

  /* {END EMPLOYEE SETTINGS} */

  //Gestisce visione della tabella in base al ruolo e al dipartimento
  function handleRoleTab() {
    if (parseInt(user.id_role) === roles.admin) {
      if (selectDepartment === "customerCare") {
        return (
          <TabellaCustomerCare
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={customerCareUsers}
            tickets={tabellaCc}
          />
        );
      } else if (selectDepartment === "officina") {
        return (
          <TabellaOfficina
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={officinaUsers}
            tickets={tabellaOfficina}
          />
        );
      } else if (selectDepartment === "venditori") {
        return (
          <TabellaVenditori
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={venditoriUsers}
            tickets={tabellaContratti}
          />
        );
      }
    } else {
      if (parseInt(user.idModule) === modules.customerCare) {
        return (
          <TabellaCustomerCare
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={customerCareUsers}
            tickets={tabellaCc}
          />
        );
      } else if (parseInt(user.idModule) === modules.officina) {
        return (
          <TabellaOfficina
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={officinaUsers}
            tickets={tabellaOfficina}
          />
        );
      } else if (parseInt(user.idModule) === modules.venditori) {
        return (
          <TabellaVenditori
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={venditoriUsers}
            tickets={tabellaContratti}
          />
        );
      }
    }
  }

  //Gestisce visione della progress bar e torta in base al ruolo e al dipartimento
  function handleRoleStatusBar() {
    if (parseInt(user.id_role) === roles.admin) {
      if (selectDepartment === "customerCare") {
        return (
          <StatusBarCustomerCareDaily
            interval={interval}
            dipendente={dipendente}
            user={user}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={customerCareUsers}
            dati={dati}
            loading={loadingDati}
          />
        );
      } else if (selectDepartment === "officina") {
        return (
          <StatusBarOfficina
            interval={interval}
            dipendente={dipendente}
            user={user}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={officinaUsers}
            dati={dati}
            loading={loadingDati}
          />
        );
      }
    } else {
      if (parseInt(user.idModule) === modules.customerCare) {
        return (
          <StatusBarCustomerCareDaily
            interval={interval}
            dipendente={dipendente}
            user={user}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={customerCareUsers}
            dati={dati}
            loading={loadingDati}
          />
        );
      } else if (parseInt(user.idModule) === modules.officina) {
        return (
          <StatusBarOfficina
            interval={interval}
            dipendente={dipendente}
            user={user}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={officinaUsers}
            dati={dati}
            loading={loadingDati}
          />
        );
      }
    }
  }

  //Gestisce visione dei grafici in base al ruolo e al dipartimento
  function handleRoleChart() {
    if (parseInt(user.id_role) === roles.admin) {
      if (selectDepartment === "customerCare") {
        return (
          <ChartDipendenti
            interval={interval}
            dipendente={dipendente}
            setInterval={setInterval}
            setDipendente={setDipendente}
            users={customerCareUsers}
            ticket={ticket}
            target={target}
            loading={loading}
            selectionRange={selectionRange}
            handleSelect={handleSelect}
          />
        );
      } else if (selectDepartment === "officina") {
        return (
          <ChartOfficina
            interval={interval}
            dipendente={dipendente}
            users={officinaUsers}
            setInterval={setInterval}
            setDipendente={setDipendente}
            ticket={ticket}
            target={target}
            loading={loading}
            selectionRange={selectionRange}
            handleSelect={handleSelect}
          />
        );
      } else if (selectDepartment === "venditori") {
        return (
          <ChartVenditori
            interval={interval}
            dipendente={dipendente}
            users={venditoriUsers}
            setInterval={setInterval}
            setDipendente={setDipendente}
            ticket={sellChart}
            count={count}
            setCount={setCount}
            target={target}
            loading={loading}
            selectionRange={selectionRange}
            handleSelect={handleSelect}
          />
        );
      }
    } else {
      console.log(user.idModule);
      if (parseInt(user.idModule) === modules.customerCare) {
        return (
          <ChartDipendenti
            interval={interval}
            dipendente={dipendente}
            users={customerCareUsers}
            setInterval={setInterval}
            setDipendente={setDipendente}
            ticket={ticket}
            target={target}
            loading={loading}
            selectionRange={selectionRange}
            handleSelect={handleSelect}
          />
        );
      } else if (parseInt(user.idModule) === modules.officina) {
        return (
          <ChartOfficina
            interval={interval}
            dipendente={dipendente}
            users={officinaUsers}
            setInterval={setInterval}
            setDipendente={setDipendente}
            ticket={ticket}
            target={target}
            loading={loading}
            selectionRange={selectionRange}
            handleSelect={handleSelect}
          />
        );
      } else if (parseInt(user.idModule) === modules.venditori) {
        return (
          <ChartVenditori
            interval={interval}
            dipendente={dipendente}
            users={officinaUsers}
            setInterval={setInterval}
            setDipendente={setDipendente}
            ticket={sellChart}
            target={target}
            count={count}
            setCount={setCount}
            loading={loading}
            selectionRange={selectionRange}
            handleSelect={handleSelect}
          />
        );
      }
    }
  }

  useEffect(() => {
    if (
      (parseInt(user.id_role) === roles.admin &&
        selectDepartment === "customerCare") ||
      (parseInt(user.id_role) === roles.hr &&
        parseInt(user.idModule) === modules.customerCare)
    ) {
      GetAllTicketSupervisor();
      GetCustomerCareUsers();
      GetTicketChartSupervisor();
      CalculateTargetSupervisor();
    } else if (
      (parseInt(user.id_role) === roles.admin &&
        selectDepartment === "officina") ||
      (parseInt(user.id_role) === roles.hr &&
        parseInt(user.idModule) === modules.officina)
    ) {
      GetAllWorksSupervisor();
      GetOfficinaUsers();
      GetMinuteOfficinaSupervisor();
      CalculateOfficinaTargetSupervisor();
    } else if (
      (parseInt(user.id_role) === roles.admin &&
        selectDepartment === "venditori") ||
      (parseInt(user.id_role) === roles.hr &&
        parseInt(user.idModule) === modules.officina)
    ) {
      GetVenditoriUsers();
      GetAllContrattiSupervisor();
      CalculateGraficoVenditoriSupervisor();
    } else if (
      parseInt(user.id_role) !== roles.admin &&
      parseInt(user.id_role) !== roles.hr &&
      parseInt(user.idModule) === modules.officina
    ) {
      GetAllWorksEmployee();
      GetNumberWorksEmployee();
      CalculateOfficinaTargetEmployee();
    } else if (
      parseInt(user.id_role) !== roles.admin &&
      parseInt(user.id_role) !== roles.hr &&
      parseInt(user.idModule) === modules.customerCare
    ) {
      GetAllTicketEmployee();
      CalculateTargetEmployee();
      GetNumberCallsTest();
    } else if (
      parseInt(user.id_role) !== roles.admin &&
      parseInt(user.id_role) !== roles.hr &&
      parseInt(user.idModule) === modules.venditori
    ) {
      GetAllContrattiEmployee();
      CalculateGraficoVenditoriEmployee();
    }
  }, [count, selectDepartment, dipendente, startDate, endDate]);

  return (
    <div className="col-12">
      <Card>
        {user.id_role === "4" ? (
          <div className="col-12 my-2">
            <label className=" col-12 mb-4 mt-6">
              Seleziona tipo dipartimento
            </label>
            <select
              onChange={(e) => {
                setSelectDepartment(e.target.value);
                setDipendente("media");
              }}
              className="form-control mb-6"
            >
              <option value="customerCare">Customer Care</option>
              <option value="officina">Officina</option>
              <option value="venditori">Venditori</option>
            </select>
          </div>
        ) : null}
        {handleRoleStatusBar()}
      </Card>

      {handleRoleChart()}
      {(parseInt(user.id_role) === roles.admin &&
        selectDepartment === "venditori") ||
      parseInt(user.idModule) === modules.venditori ? (
        <TestDrive dipendente={dipendente} />
      ) : null}

      <Card className="mt-3">
        <Card.Header>
          <h6 style={{ color: "white" }} className={classes.heading}>
            TABELLA PROGRESSI
          </h6>
        </Card.Header>
        {handleRoleTab()}
      </Card>
      <ModalCustomerCare show={show} handleClose={handleClose} />
    </div>
  );
}
