import React, { useState } from "react";
import { roles } from "../../../../config/config";
import { Button } from "react-bootstrap";
import "../../../../style.css";
import { CSVLink } from "react-csv";
import { useSelector } from "react-redux";
import { Card } from "react-bootstrap";
import { Chip } from "@material-ui/core";
import {
  Grid,
  Table,
  PagingPanel,
  TableHeaderRow,
  TableGroupRow,
  Toolbar,
  ColumnChooser,
  TableColumnVisibility,
} from "@devexpress/dx-react-grid-bootstrap4";
import { DataTypeProvider } from "@devexpress/dx-react-grid";
import {
  PagingState,
  IntegratedPaging,
  GroupingState,
  IntegratedGrouping,
} from "@devexpress/dx-react-grid";
import "@devexpress/dx-react-grid-bootstrap4/dist/dx-react-grid-bootstrap4.css";
import { ModalImportVenditori } from "../mixed/ModalImportVenditori";

export const TabellaVenditori = (props) => {
  const { user } = useSelector((state) => state.auth);

  const [importModal, setImportModal] = useState(false);

  const handleOpenImport = () => setImportModal(true);
  const handleCloseImport = () => setImportModal(false);

  const [pageSizes] = useState([10, 25, 50, 100, 0]);
  const [contratto] = useState(["stato"]);

  const columns = [
    { name: "", title: "" },
    { name: "idTrattativa", title: "Trattativa" },
    { name: "idVenditore", title: "ID venditore" },
    { name: "venditore", title: "Venditore" },
    { name: "codiceOfferta", title: "Codice offerta" },
    { name: "stato", title: "Stato trattiva" },
  ];

  const rows = props.tickets;

  const PresenzaFormatter = ({ value }) => {
    var color;
    switch (value) {
      case "PREVENTIVO":
        color = "#f9c74f";
        break;
      case "CONTRATTO":
        color = "#90be6d";
        break;
      case "MANCATA VENDITA":
        color = "#d00000";
        break;
      default:
        color = "#8a817c";
    }
    return (
      <>
        <Chip
          size="large"
          label={value}
          icon={
            <i style={{ color: color }} className="fas fa-circle fa-sm"></i>
          }
        />
      </>
    );
  };

  const CurrencyTypeProvider = (props) => (
    <DataTypeProvider formatterComponent={PresenzaFormatter} {...props} />
  );

  const TableComponent = ({ ...restProps }) => (
    <Table.Table {...restProps} className="table-striped" />
  );

  return (
    <>
      <ModalImportVenditori
        handleCloseImport={handleCloseImport}
        importModal={importModal}
      />
      <div className="row">
        {parseInt(user.id_role) === roles.hr ||
        parseInt(user.id_role) === roles.admin ? (
          <div className="col-6 float-left">
            <select
              value={props.dipendente}
              onChange={(e) => {
                props.setDipendente(e.target.value);
              }}
              className="form-control ml-4 mt-4 mb-4"
            >
              <option>Seleziona dipendente</option>
              {props.users.map((e, key) => {
                return (
                  <option key={key} value={e.idDipendente}>
                    {e.fullname}
                  </option>
                );
              })}
            </select>
          </div>
        ) : null}
        <div className="col-12 mt-4">
          <Card className="col-12">
            <Grid rows={rows} columns={columns}>
              <CurrencyTypeProvider for={contratto} />
              <GroupingState grouping={[{ columnName: "idTrattativa" }]} />
              <IntegratedGrouping />
              <PagingState defaultCurrentPage={0} defaultPageSize={10} />
              <IntegratedPaging />
              <Table tableComponent={TableComponent} />
              <TableHeaderRow />
              <TableGroupRow />
              <PagingPanel pageSizes={pageSizes} />
              <TableColumnVisibility />
              <Toolbar />
              <ColumnChooser />
            </Grid>
          </Card>
        </div>
        <div className="col-6">
          {user.aggiungiVendita === true ? (
            <div style={{ float: "left" }}>
              <Button
                onClick={() => props.handleShow()}
                className="mt-4 mb-4"
                variant="primary"
                size="sm"
                style={{ float: "left", marginBottom: "2%" }}
              >
                Aggiungi vendita
              </Button>
            </div>
          ) : null}
          {user.importCsv === true &&
          (parseInt(user.id_role) === roles.hr ||
            parseInt(user.id_role) === roles.admin) ? (
            <div style={{ float: "left" }}>
              <Button
                onClick={() => handleOpenImport()}
                variant="primary"
                className="mt-4 mb-4 ml-4"
                size="sm"
                style={{ float: "left", marginBottom: "2%" }}
              >
                Import CSV
              </Button>
            </div>
          ) : null}
        </div>
        {user.exportCsv === true &&
        (parseInt(user.id_role) === roles.hr ||
          parseInt(user.id_role) === roles.admin) ? (
          <div className="col-6">
            <CSVLink
              data={props.tickets}
              filename={"trattative.csv"}
              className="btn btn-primary btn-sm font-weight-bold mt-4 mb-4 mr-4"
              style={{ marginBottom: "1%", marginRight: "2%", float: "right" }}
              size={"small"}
              separator={";"}
            >
              Esporta CSV{" "}
            </CSVLink>
          </div>
        ) : null}
      </div>
    </>
  );
};
