import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import it from "date-fns/locale/it";
import { DateRangePicker } from "react-date-range";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import {
  defaultStaticRanges,
  defaultInputRanges,
} from "react-date-range/dist/defaultRanges";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Typography,
} from "@material-ui/core";

import { scaleBand } from "@devexpress/dx-chart-core";
import { ArgumentScale } from "@devexpress/dx-react-chart";
import "react-date-range/dist/styles.css"; // main style file
import { line, curveStep } from "d3-shape";
import "react-date-range/dist/theme/default.css"; // theme css file
import config, { roles } from "../../../../config/config";
import {
  Chart,
  ArgumentAxis,
  ValueAxis,
  BarSeries,
  LineSeries,
  Title,
  Legend,
} from "@devexpress/dx-react-chart-material-ui";
import { Card, Tabs, Tab, ProgressBar, Row } from "react-bootstrap";
import { ValueScale, Animation, Stack } from "@devexpress/dx-react-chart";
import { Skeleton } from "@material-ui/lab";

export const ChartVenditori = (props) => {
  /*eslint-disable */
  const { user } = useSelector((state) => state.auth);

  const [dati, setDati] = useState([]);
  const [loading, setLoading] = useState(false);
  const [percDay, setPercDay] = useState(0);
  const [percWeek, setPercWeek] = useState(0);
  const [colorDaily, setColorDaily] = useState("red");
  const [colorWeek, setColorWeek] = useState("red");

  const handlepercDay = (minRaggiunti, obiettivo) => {
    setPercDay(Math.round((minRaggiunti / obiettivo) * 100));
  };
  const handlepercWeek = (minRaggiunti, obiettivo) => {
    const newLocal = obiettivo * 5;
    setPercWeek(Math.round((minRaggiunti / newLocal) * 100));
  };

  const ColorDay = () => {
    if (percDay < 25) {
      setColorDaily("#e63946");
    } else if (percDay >= 25 && percDay <= 50) {
      setColorDaily("#f4a261");
    } else if (percDay > 50 && percDay <= 75) {
      setColorDaily("#e9c46a");
    } else if (percDay > 75) {
      setColorDaily("#43aa8b");
    }
  };

  const ColorWeek = () => {
    if (percWeek < 25) {
      setColorWeek("#e63946");
    } else if (percWeek >= 25 && percWeek <= 50) {
      setColorWeek("#f4a261");
    } else if (percWeek > 50 && percWeek <= 75) {
      setColorWeek("#e9c46a");
    } else if (percWeek > 75) {
      setColorWeek("#43aa8b");
    }
  };

  const FillProgressCircularBar = () => {
    setLoading(true);
    const options = {
      headers: {
        Authorization:
          "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
      },
      method: "POST",
      body: JSON.stringify({
        idUser: user.id,
        idDipendente: props.dipendente,
        idRuolo: user.id_role,
        idOrganization: user.id_organization,
      }),
    };
    fetch(config.apiUrl + "obiettivi/FillProgressCircular.php", options)
      .then((response) => response.json())
      .then((data) => setDati(data))
      .then(() => setLoading(false));
  };

  var telefonateOggi = dati.todayCalls;
  var obiettivoGiornaliero = dati.targetGiornaliero;
  var telefonateSettimana = dati.weeklyCalls;
  var obiettivoSettimana = dati.targetSettimanale;
  var obiettivoMinuti = dati.minutiProduttivita;
  var minutiRaggiunti = dati.minutiRaggiunti;
  var minutiSettimanaliProduttivita = dati.minutiSettimanaliProduttivita;

  useEffect(() => {
    handlepercDay(minutiRaggiunti, obiettivoMinuti);
    handlepercWeek(minutiSettimanaliProduttivita, obiettivoMinuti);
    ColorDay();
    ColorWeek();
  }, [dati]);

  useEffect(() => {
    FillProgressCircularBar();
  }, [props.count, props.dipendente]);

  function chiamateGiorno() {
    var nGiorno = Math.round((100 * telefonateOggi) / obiettivoGiornaliero);
    //setPercentuale(Math.round(100*dati.minutiRaggiunti/dati.minutiProduttivita))
    return nGiorno;
  }

  function chiamateSettimana() {
    var nSettimana = Math.round(
      (100 * telefonateSettimana) / obiettivoSettimana
    );
    return nSettimana;
  }

  function formatHours(hour) {
    var num = hour;
    var hours = num / 60;
    var rhours = Math.floor(hours);
    var minutes = (hours - rhours) * 60;
    var rminutes = Math.round(minutes);

    if (rminutes > 1 || rminutes === 0) {
      var textMin = " MINUTI";
    } else {
      textMin = " MINUTO";
    }

    if (rhours > 1) {
      var textHours = " ORE";
    } else {
      textHours = " ORA";
    }

    if (rhours === 0) {
      return rminutes + textMin;
    } else {
      return (
        rhours + textHours + (rminutes > 1 ? " e " + rminutes + textMin : "")
      );
    }
  }

  const Line = (props) => (
    <LineSeries.Path
      {...props}
      path={line()
        .x(({ arg }) => arg)
        .y(({ val }) => val)
        .curve(curveStep)}
    />
  );

  const Root = (props) => (
    <Legend.Root {...props} className="m-auto flex-row" />
  );

  const Marker = (props) => {
    const { className, color } = props;
    return (
      <svg className={className} fill={color} width="10" height="10">
        <rect x={0} y={0} width={100} height={100} />
      </svg>
    );
  };

  const stacks = [{ series: ["riuscite", "fallite", "attesa"] }];

  const format = () => (tick) => tick;

  const staticRangesLabels = {
    Today: "Oggi",
    Yesterday: "Ieri",
    "This Week": "Questa settimana",
    "Last Week": "Settimana scorsa",
    "This Month": "Questo mese",
    "Last Month": "Mese scorso",
  };

  const inputRangesLabels = {
    "days up to today": "giorni da oggi",
    "days starting today": "giorni a partire da oggi",
  };

  function translateRange(dictionary) {
    return (item) =>
      dictionary[item.label]
        ? { ...item, label: dictionary[item.label] }
        : item;
  }

  const itStaticRanges = defaultStaticRanges.map(
    translateRange(staticRangesLabels)
  );
  const itInputRanges = defaultInputRanges.map(
    translateRange(inputRangesLabels)
  );

  return (
    <div className="row">
      <div className="col-12 mt-2 mb-2"></div>

      <div className="col-12">
        <>
          <Tabs
            onSelect={() => props.setCount(props.count + 1)}
            defaultActiveKey="vendite"
            id="uncontrolled-tab-example"
          >
            <Tab eventKey="vendite" title="Grafico vendite">
              <Card>
                <Card.Header>
                  <h6 className="card-title-h3">
                    GRAFICO RIEPILOGO - SELEZIONA INTERVALLO DATE
                  </h6>
                </Card.Header>
                <Card.Body>
                  <Accordion
                    style={{ backgroundColor: "#ffff" }}
                    className="mt-4 mb-4 col-7 mx-auto"
                  >
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="panel1a-content"
                      id="panel1a-header"
                    >
                      <Typography>
                        Seleziona intervallo{" "}
                        {parseInt(user.id_role) === roles.admin ||
                        parseInt(user.id_role) === roles.hr
                          ? "- Dipendente"
                          : null}
                      </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      <div className="row">
                        {parseInt(user.id_role) === roles.admin ||
                        parseInt(user.id_role) === roles.hr ? (
                          <select
                            value={props.dipendente}
                            onChange={(e) => {
                              props.setDipendente(e.target.value);
                            }}
                            className="form-control col-12"
                          >
                            <option>Seleziona dipendente</option>
                            {props.users.map((e, key) => {
                              return (
                                <option key={key} value={e.idDipendente}>
                                  {e.fullname}
                                </option>
                              );
                            })}
                          </select>
                        ) : null}
                        <div>
                          <DateRangePicker
                            staticRanges={itStaticRanges}
                            inputRanges={itInputRanges}
                            className="col-12 mt-3"
                            locale={it}
                            ranges={[props.selectionRange]}
                            onChange={props.handleSelect}
                          />
                        </div>
                      </div>
                    </AccordionDetails>
                  </Accordion>
                  {!props.loading ? (
                    <Chart
                      data={
                        props.ticket !== []
                          ? props.ticket
                          : [{ month: "NO DATA", minuti: 0 }]
                      }
                    >
                      <ValueScale name="minuti" />
                      <ArgumentScale factory={scaleBand} />
                      <ArgumentAxis tickFormat={format} />
                      <ValueAxis scaleName="minuti" showLine showTicks />
                      <BarSeries
                        name="Riuscite"
                        valueField="riuscite"
                        argumentField="month"
                        scaleName="minuti"
                        color="#2a9d8f"
                      />
                      <BarSeries
                        name="Fallite"
                        valueField="fallite"
                        argumentField="month"
                        scaleName="minuti"
                        color="#d62246"
                      />
                      <BarSeries
                        name="In attesa"
                        valueField="attesa"
                        argumentField="month"
                        scaleName="minuti"
                        color="#f0cf65"
                      />
                      <LineSeries
                        name="Obiettivo vendite"
                        valueField="obiettivo"
                        argumentField="month"
                        scaleName="minuti"
                        color="#2b2d42"
                        seriesComponent={Line}
                      />
                      <Animation />
                      <Legend
                        position="right"
                        rootComponent={Root}
                        markerComponent={Marker}
                      />
                      <Title text="Grafico venditori" />
                      <Stack stacks={stacks} />
                    </Chart>
                  ) : (
                    <div className="row ml-4">
                      <div className="col-2">
                        <Skeleton animation="wave" height={500} width={50} />
                      </div>
                      <div className="col-2">
                        <Skeleton animation="wave" height={500} width={50} />
                      </div>
                      <div className="col-2">
                        <Skeleton animation="wave" height={500} width={50} />
                      </div>
                      <div className="col-2">
                        <Skeleton animation="wave" height={500} width={50} />
                      </div>
                      <div className="col-2">
                        <Skeleton animation="wave" height={500} width={50} />
                      </div>
                      <div className="col-2">
                        <Skeleton animation="wave" height={500} width={50} />
                      </div>
                    </div>
                  )}
                </Card.Body>
              </Card>
            </Tab>
            <Tab eventKey="testDrive" title="Progressi test drive">
              <Card>
                <Card.Header>
                  <h6 className="card-title-h3">PROGRESSI TEST DRIVE</h6>
                </Card.Header>
                <Card.Body>
                  <Row>
                    {parseInt(user.id_role) === roles.admin ||
                    parseInt(user.id_role) === roles.hr ? (
                      <div className="col-12">
                        <select
                          value={props.dipendente}
                          onChange={(e) => {
                            props.setDipendente(e.target.value);
                          }}
                          className="form-control col-6"
                        >
                          <option>Seleziona dipendente</option>
                          {props.users.map((e, key) => {
                            return (
                              <option key={key} value={e.idDipendente}>
                                {e.fullname}
                              </option>
                            );
                          })}
                        </select>
                      </div>
                    ) : null}
                    <div className="col-6 mt-8 my-8">
                      <div className="col-12 mt-10">
                        <label className="col-12">
                          Test drive effettuati oggi:{" "}
                          <strong>
                            {!props.loading
                              ? telefonateOggi + "/" + obiettivoGiornaliero
                              : "Caricamento..."}
                          </strong>
                        </label>
                        {!loading ? (
                          <ProgressBar
                            animated
                            variant="success"
                            now={chiamateGiorno()}
                            label={`${chiamateGiorno()}%`}
                          />
                        ) : (
                          <Skeleton animation="wave" width={540} />
                        )}
                      </div>
                      <div className="col-12 mt-20">
                        <label className="col-12">
                          Test drive settimanali:{" "}
                          <strong>
                            {!loading
                              ? telefonateSettimana + "/" + obiettivoSettimana
                              : "Caricamento..."}
                          </strong>
                        </label>
                        {!loading ? (
                          <ProgressBar
                            animated
                            variant="info"
                            now={chiamateSettimana()}
                            label={`${chiamateSettimana()}%`}
                          />
                        ) : (
                          <Skeleton animation="wave" width={540} />
                        )}
                      </div>
                    </div>
                    <div className="col-3 mt-8 my-8">
                      <div className="col-12">
                        <label className="col-12 text-center">
                          <b>Obiettivo test drive quotidiano:</b> <br />
                          {formatHours(minutiRaggiunti)} SU{" "}
                          {formatHours(obiettivoMinuti)}
                        </label>
                        <div
                          className="d-flex ml-10"
                          style={{ width: 150, height: 150 }}
                        >
                          {!loading ? (
                            <CircularProgressbar
                              styles={buildStyles({
                                pathColor: colorDaily,
                                textColor: colorDaily,
                              })}
                              className="ml-4"
                              value={percDay}
                              text={`${percDay}%`}
                            />
                          ) : (
                            <Skeleton
                              className="ml-4"
                              variant="circle"
                              animation="wave"
                              width={140}
                              height={140}
                            />
                          )}
                        </div>
                      </div>
                    </div>
                    <div className="col-3 mt-8 my-8">
                      <div className="col-12">
                        <label className="col-12 text-center">
                          <b>Obiettivo test drive settimana precedente:</b>{" "}
                          <br />
                          {formatHours(minutiSettimanaliProduttivita)} SU{" "}
                          {formatHours(obiettivoMinuti * 5)}
                        </label>
                        <div
                          className="d-flex ml-10"
                          style={{ width: 150, height: 150 }}
                        >
                          {!loading ? (
                            <CircularProgressbar
                              styles={buildStyles({
                                pathColor: colorWeek,
                                textColor: colorWeek,
                              })}
                              className="ml-4"
                              value={percWeek}
                              text={`${percWeek}%`}
                            />
                          ) : (
                            <Skeleton
                              className="ml-4"
                              variant="circle"
                              animation="wave"
                              width={140}
                              height={140}
                            />
                          )}
                        </div>
                      </div>
                    </div>
                  </Row>
                </Card.Body>
              </Card>
            </Tab>
          </Tabs>
        </>
      </div>
    </div>
  );
};
